import React, { Component } from 'react';
import { connect } from "react-redux";

class Projects extends Component {
  render() {
    const { user } = this.props;
    const projects = user.projects.map((project, index) => <div key={ 'project-' + index }>
        <div className="title">{ project.name }</div>
        <div>{ project.description }</div>
        <div className="profile-skills">{ project.technologies.map((technology, i) => <div key={ 'technology-' + i } className="profile-skill">{ technology }</div>) }</div>
      </div>);
    return (
      <div>
        <div className="profile-title">Projects</div>
        <div>{ projects }</div>
      </div>
    );
  }
}

export default connect((store) => {
  return {
    user: store.user
  };
})(Projects);



