import React, { Component } from 'react';

class Footer extends Component {
  render() {
    const { user } = this.props;
    return (
      <footer>
        <div className="app-content">
          <div className="footer-links">
            <div className="footer-social-links">
              <a href={user.social.linkedinUrl} target='_blank'><i className="fa fa-linkedin"></i></a>
              <a href={user.social.githubUrl} target='_blank'><i className="fa fa-github"></i></a>
              <a href={user.social.facebookUrl} target='_blank'><i className="fa fa-facebook"></i></a>
              <a href={user.social.twitterUrl} target='_blank'><i className="fa fa-facebook"></i></a>
            </div>
            <div><i className="fa fa-envelope"></i>{user.email}</div>
          </div>
        </div>
      </footer>
    );
  }
}

export default Footer;



