import React, { Component } from 'react';
import { connect } from "react-redux";
import ReactMapboxGl, { Marker, ZoomControl } from "react-mapbox-gl";
import Message from './Message';

class Contact extends Component {
  render() {
    const { user } = this.props;
    const Map = ReactMapboxGl({
      accessToken: 'pk.eyJ1IjoiZGhhdmFsemFsYSIsImEiOiJjamV4dDdyMDcxYzZjMzNtamM2MGlwNmU5In0.DbXScKov5NtbEPa261Ob8A'
    });
    return (
      <div className="contact">
        <div className="contact-content">
          <div className="contact-main-content">
            <div className="profile-title">Contact</div>
            <div className="contact-name">{ `${user.firstName} ${user.lastName}`}</div>
            <div>{ user.address }</div>
            <div className="contact-email"><i className="fa fa-envelope"></i>{user.email}</div>
            <div className="contact-social-links">
              <a href={user.social.linkedinUrl} target='_blank'><i className="fa fa-linkedin"></i></a>
              <a href={user.social.githubUrl} target='_blank'><i className="fa fa-github"></i></a>
              <a href={user.social.facebookUrl} target='_blank'><i className="fa fa-facebook"></i></a>
              <a href={user.social.twitterUrl} target='_blank'><i className="fa fa-facebook"></i></a>
            </div>

            <Map
               style="mapbox://styles/mapbox/streets-v9"
               center={[78.491684, 17.387140]}
               className="contact-map"
               containerStyle={{
                 width: "calc(100vw - 30px)",
                 maxWidth: window.innerWidth > 768 ? 300 : 768,
                 height: 300
               }}
               zoom={[4]}>
                 <ZoomControl />
                 <Marker
                   coordinates={[78.491684, 17.387140]}
                   anchor="bottom">
                   <i className="fa fa-map-marker"></i>
                 </Marker>
             </Map>
          </div>
          <Message />
        </div>
      </div>
    );
  }
}

export default connect((store) => {
  return {
    user: store.user
  };
})(Contact);



