import React, { Component } from 'react';
import { connect } from "react-redux";

class About extends Component {
  constructor(props) {
    super(props);
    this.state = {
      user: props.user,
      repos: []
    }
  }

  render() {
    const { user } = this.props;
    var skills = user.skills.map((skill) => <div key={skill} className="profile-skill"> {skill} </div>);

    // get github repos
    if(this.state.repos.length === 0) {
      this.gitHubRepos(user.social.github);
    }

    return (
      <div className="about">
        <div className="profile-header">
          <img className="profile-image" align="left" src={user.profile.url} />
          <div className="profile-title">{user.profile.title}</div>
          <div className="profile-description">( {user.profile.description} )</div>
          <div className="profile-skills">
            {skills}
          </div>
        </div>
        <div className="profile-full-description">{ user.profile.fulldescription }</div>
        <div className="profile-short-description">{ user.profile.shortdescription }</div>
        <div className='title'>Education</div>
        <div className='education'>
          {
            user.education.map((education, index) => (
              <div className='education-detail' key={ 'education-' + index }>{education.name}</div>
            ))
          }
        </div>

        <div className='title'>Work Experience</div>
        <div className='work-experiences'>
          {
            user.experiences.map((experience, index) => (
              <div className='experience-detail' key={ 'experience-' + index }>
                <div className='experience-company'>{experience.company}</div>
                <div className='experience-designation'>{experience.designation}</div>
                <div className='experience-duration'>{experience.duration}</div>
              </div>
            ))
          }
        </div>

        <div className='title'>Github Repositories ( <a href={'https://github.com/' + user.social.github } target="_blank"> {user.social.github} </a> )</div>
        <div className='github-repos'>
          {
            this.state.repos.map((repo) => (
              <a href={repo.html_url} target='_blank' className='repo' key={ 'repo-' + repo.id }>{repo.name}</a>
            ))
          }
        </div>
      </div>
    );
  }

  gitHubRepos (username) {
    return fetch(`https://api.github.com/users/${username}/repos`).then((response) => {
      if (response.status === 200) {
        return response.json();
      }
    }).then((repos) => {
      if(repos) {
        this.setState(prevState => (Object.assign({}, prevState, { repos: repos })));
      }
    });
  };
}

export default connect((store) => {
  return {
    user: store.user
  };
})(About);



