import React, { Component } from 'react';
import { Link } from 'react-router-dom';

class Header extends Component {
  render() {
    const { user } = this.props;
    const links = [{
      name: 'About',
      url: '/about'
    }, {
      name: 'Projects',
      url: '/projects'
    }, {
      name: 'Contact',
      url: '/contact'
    }];

    var navLinks = links.map((link) => <Link key={link.name} className="header-nav-link" to={link.url}>{link.name}</Link>);

    return (
      <header>
        <div className="app-content">
          <Link to="/" className="app-title">{user.firstName} {user.lastName}</Link>
          <div className="toggle-menu-button" onClick={this.toggleMenu}><i className="fa fa-align-justify"></i></div>
          <div className="header-nav-links" onClick={this.toggleMenu}>{navLinks}</div>
        </div>
      </header>
    );
  }

  toggleMenu() {
    let toggleMenuButton = document.querySelector(".toggle-menu-button");
    if(toggleMenuButton && toggleMenuButton.offsetParent) {
      let links = document.querySelector(".header-nav-links");
      if(links) {
        links.classList.toggle("active");
      }
    }
  }
}

export default Header;



